<template>
	<section>
		<!--查询条件-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="queryForm" ref="queryForm">
				<el-form-item label="转换名称">
					<el-input placeholder="转换名称" v-model="queryform.name"></el-input>
				</el-form-item>
				<el-form-item label="转换更新时间">
					<div class="block">
						<el-date-picker type="daterange" placeholder="选择日期范围" v-model="queryform.createdDate"></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item label="转换状态">
					<el-select v-model="queryform.transStatus" placeholder="请选择转换状态">
						<el-option label="准备" value="0"></el-option>
						<el-option label="传输中" value="1"></el-option>
						<el-option label="已停止" value="2"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers">查询</el-button>
					<el-button @click="resetForm()">重置</el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!-- 列表 -->
		<el-table :data="translist" border highlight-current-row v-loading="loading" style="width: 100%;">
			<el-table-column align="center" prop="idTransformation" label="转换编号" width="160" sortable>
			</el-table-column>
			<el-table-column prop="name" align="center" label="转换名称" width="160" sortable>
			</el-table-column>
			<el-table-column prop="createdDate" align="center" label="转换更新时间" width="160" sortable>
			</el-table-column>
			<el-table-column prop="transStatus" align="center" label="转换状态" :formatter="formatTransState" width="160" sortable>
			</el-table-column>
			<el-table-column label="操作" align="center">
				<template scope="scope">
					<el-button class="opsbtn" size="mini" @click="handleStart(scope.$index, scope.row)"><i class="el-icon-caret-right el-icon--left"></i>启动</el-button>
					<el-button class="opsbtn" size="mini" @click="handleView(scope.$index, scope.row)"><i class="el-icon-view el-icon--left"></i>查看</el-button>
					<el-button class="opsbtn" size="mini" @click="handleAdd(scope.$index, scope.row)"><i class="el-icon-view el-icon--left"></i>新增</el-button>
					<el-button class="opsbtn" size="mini" @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit el-icon--left"></i>编辑</el-button>
					<el-button class="opsbtn" size="mini" @click="handleDel(scope.$index, scope.row)"><i class="el-icon-delete el-icon--left"></i>删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--工具条-->
		<el-col :span="24" class="toolbar pagebar">
			<el-pagination class="pageitem" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentpage" :page-sizes="showpages" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="totalpage"></el-pagination>
		</el-col>

		<!--dialog 新增 查看 修改 删除-->
		<!--<el-dialog title="dialogtitle" v-model="dialongVisible" :close-on-click-modal="false">
			<el-form :model="dialogform" label-width="80px" :rules="addFormRules" ref="dialogform">
				<el-form-item label="转换名称" prop="name">
					<el-input v-model="dialogform.name" placeholder="转换名称" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="转换更新时间">
					<el-date-picker v-model="dialogform.createdDate" type="date" placeholder="转换更新时间"></el-date-picker>
				</el-form-item>
				<el-form-item label="长度">
					<el-input-number v-model="dialogform.size_rowset" :min="0" :max="9999"></el-input-number>
				</el-form-item>
				<el-form-item label="生日">
					<el-date-picker type="date" placeholder="选择日期" v-model=""></el-date-picker>
				</el-form-item>
				<el-form-item label="地址">
					<el-input type="textarea" v-model=""></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>-->
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getTransList} from '../../api/api';
	export default {
		data() {
			return {
				translist: [],
				queryform: {
					idTransformation: '',
					name: '',
					createdDate: '',
					transStatus: ''
				},
				dialogform: {
					idTransformation: '',
					name: '',
					createdDate: '',
					transStatus: '',
					description: '',
					created_user: '',
					size_rowset: 0
				},
				loading: false,
				currentpage: 1,
				totalpage: 0,
				pagesize: 5,
				showpages: [5,10,15,20],
				dialongVisible: false,
				dialogtitle: ''
			}
		},
		methods: {
			handleView: function() {

			},
			formatTransState: function (row, column) {
				return row.transStatus == 0 ? '准备' : row.transStatus == 1 ? '传输中' : '已停止';
			},
			getTransList: function() {
				let para = {
					currentPage: this.currentpage,
					pageNumber: this.pagesize
				};
				this.loading = true;
				getTransList(para).then((res) => {
					debugger
					this.translist = res.data.data;
					this.totalpage = res.data.page.totalNumber;
					this.loading = false;
				});
			},
			handleCurrentChange: function (val) {
				this.currentpage = val;
				this.getTransList();
			},
			handleSizeChange: function(val) {
				this.pagesize = val;
				this.getTransList();
			},
			resetForm: function() {
				for(let name in this.queryform) {
					this.queryform[name] ='';
				}
			}
		},
		mounted() {
			this.getTransList();
		}
	}
</script>

<style scoped lang="scss">
	@import '~scss_vars';
	.opsbtn {
		background-color: #18a689;
		border-color: #18a689;
		color: #FFFFFF;
	}
	.pagebar {
		background-color: #fff;
		.pageitem {
			float: right;
		}
	}
</style>


